video {
    position: absolute;
    width: 100%;
    left: 50%;
    top: 50%;
    height: 100%;
    object-fit: cover;
    transform: translate(-50%,-50%);
    z-Index: -1;
}


.hero-container {
    height: 30vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}

.hero-container > h1 {
    color: gold;
    font-size: 69px;
}


.fa-play-cirle {
    margin-left: 9px;
}

@media  screen and (max-width: 963px) {
    .hero-container > h1 {
        font-size: 69px;
        margin-top: 100px;
    }
}

@media  screen and (max-width: 753px) {
    .hero-container > h1 {
        font-size: 36px;
        margin-top: 25px;
    }


    .btn-mobile {
        display: block;
        text-decoration: none;
    }

    .btn {
        width: 100%;
    }
}

