:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}
.btn1 {
    background: url('../images/img-home.jpg') center center/cover no-repeat;
    font-size: 39px;
    color: gold;
    margin: 13px;
    padding: 16px 30px;
    border-radius: 3px;
    outline: none;
    border: none;
    cursor: pointer;
}
.btn2 {
    margin: 13px;
    padding: 16px 30px;
    border-radius: 3px;
    outline: none;
    border: none;
    cursor: pointer;
}
.btn--primary {
   background-color: var(--primary); 
   color: #242424;
   border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 30px;
}

.btn--medium:hover, .btn--large:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}