* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'PT Sans', sans-serif;
  }
  
  .Home {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
  }
  
  .About {
    background-image: url('./images/img-2.jpg');
    background-position: center;
    background-size: cover;
    height: 25vh;
    background-repeat: no-repeat;
    color: rgb(65, 68, 225);
    font-size: 69px;
    text-align: center;
    vertical-align: middle;
    line-height: 169px;
  }

  .About-body {
    background-image: url('./images/img-5.jpg');
    background-position: center;
    background-size: cover;
    height: 100vh;
    background-repeat: no-repeat;
    color: rgb(0, 60, 255);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #bio-content{
    padding: 50px;
    background-color: rgba(0, 255, 255, 0.986);    
  } 
  #bio-h{
    font-size: 39px;
    text-align: center;
  }
  #bio-p{
    font-size: 20px;
    text-align: left;
  }
 
  
  .Education {
    background-image: url('./images/img-1.jpg');
    background-position: center;
    background-size: cover;
    height: 25vh;
    background-repeat: no-repeat;
    color: green;
    font-size: 69px;
    text-align: center;
    vertical-align: middle;
    line-height: 169px;
  }

  .Education-body {
    background-image: url('./images/img-7.jpg');
    background-position: center;
    background-size: cover;
    height: 100vh;
    background-repeat: no-repeat; 
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #edu-content{
    padding: 50px;
    background-color: rgba(0, 255, 200, 0.986);    
  } 
  #edu-h{
    font-size: 36px;
    text-align: center;
    color: green
  }
  #edu-p{
    font-size: 20px;
    text-align: left;
    color: black;
  }
 
  .Experience {
    background-image: url('./images/img-8.jpg');
    background-position: center;
    background-size: cover;
    height: 25vh;
    background-repeat: no-repeat;
    color: white;
    font-size: 69px;
    text-align: center;
    vertical-align: middle;
    line-height: 169px;
  }
  .Experience-body {
    background-image: url('./images/exp.jpg');
    background-position: center;
    background-size: cover;
    height: 100vh;
    background-repeat: no-repeat;
    color:black;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #exp-content{
    padding: 50px;
    background-color: white    
  } 
  #exp-h{
    font-size: 30px;
    text-align: center;
  }
  #exp-p{
    font-size: 20px;
    text-align: left;
  }

  .Contact {
    background-image: url('./images/img-home.jpg');
    background-position: center;
    background-size: cover;
    height: 25vh;
    font-size: 69px;
    color: rgb(0, 247, 255);
    text-align: center;
    vertical-align: middle;
    line-height: 169px;
  }
  .Contact-body {
    background-image: url('./images/img-3.jpg');
    background-position: center;
    background-size: cover;
    height: 100vh;
    background-repeat: no-repeat; 
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  #phone-div {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  #phone {
    margin-left: 2rem;  
  }

  #email-div {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 2rem;
  }
  #email {
    margin-left: 2rem;  
  }

  #linkedIn-div {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 2rem;
  }
  #linkedIn {
    margin-left: 2rem; 
    color: blue; 
    font-size: large;
  }

  #cont-content{
    padding: 100px;
    background-color: whitesmoke;    
  } 
  #cont-h{
    font-size: 30px;
    text-align: center;
  }
  #cont-p{
    font-size: 20px;
    text-align: left;
  }

  @media only screen and (max-width: 500px) {
    #bio-content {
      margin-top: 1000px;
    }

    #edu-content {
      margin-top: 1000px;
    }

    #exp-content {
      margin-top: 1000px;
    }

    #cont-content {
      margin-top: 20px;
    }
  }

 